<template>
  <div id="magnifierArea"
       @mousemove="handleMouseMove"
       @mouseenter="handleMouseEnter"
       @mouseleave="handleMouseLeave"
       @touchstart="handleTouchStart"
       @touchmove="handleTouchMove"
       @touchend="handleTouchEnd"
       style="position: relative; touch-action: none;">
    <!-- Preview image -->
    <img :src="imagePreviewUrl" ref="previewImg" @load="updateAreaDimensions" style="display: block; width: 100%;" />

    <!-- Mask overlay on preview image -->
    <div class="magnifier-mask" :style="maskStyle"></div>

    <!-- Zoom container showing the magnified area -->
    <div class="magnifier-zoom" :style="zoomStyle">
      <img :src="imageUrl" :style="zoomImgStyle" />
    </div>

    <!-- Zoom Factor Adjuster (only slider guide and dot) -->
    <div class="zoom-adjuster">
      <input type="range" min="0.25" max="1" step="0.01" v-model.number="zoomFactor" class="zoom-slider" />
    </div>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";

export default {
  extends: _Services,
  props: {
    zoomFactor: {
      type: Number,
      default: 0.5
    },
    zoomWidth: {
      type: String,
      default: "70vw"
    },
    zoomHeight: {
      type: String,
      default: "20vh"
    }
  },
  data() {
    return {
      areaWidth: 0,
      areaHeight: 0,
      maskX: 0,
      maskY: 0,
      showZoom: false,
    };
  },
  computed: {
    imageUrl() {
      return this.$read(this.locus);
    },
    // Updated: if areaWidth is not set, return the original imageUrl so that the image loads
    imagePreviewUrl() {
      if (!this.areaWidth) return this.imageUrl;
      return this.imageUrl.concat("?w=" + this.areaWidth);
    },
    zoomWidthPx() {
      return this.parseDimension(this.zoomWidth);
    },
    zoomHeightPx() {
      return this.parseDimension(this.zoomHeight);
    },
    computedMaskWidth() {
      return this.zoomWidthPx * this.zoomFactor;
    },
    computedMaskHeight() {
      return this.zoomHeightPx * this.zoomFactor;
    },
    maskStyle() {
      return {
        position: 'absolute',
        left: this.maskX + 'px',
        top: this.maskY + 'px',
        width: this.computedMaskWidth + 'px',
        height: this.computedMaskHeight + 'px',
        backgroundColor: 'rgba(0,0,0,0.3)',
        pointerEvents: 'none',
        display: this.showZoom ? 'block' : 'none',
        zIndex: 5
      };
    },
    zoomStyle() {
      return {
        position: 'absolute',
        left: (this.maskX + this.computedMaskWidth / 2 - this.zoomWidthPx / 2) + 'px',
        top: (this.maskY - this.zoomHeightPx - 10) + 'px',
        width: this.zoomWidthPx + 'px',
        height: this.zoomHeightPx + 'px',
        overflow: 'hidden',
        display: this.showZoom ? 'block' : 'none',
        zIndex: 10
      };
    },
    zoomImgStyle() {
      const effectiveScale = 1 / this.zoomFactor;
      const maskCenterX = this.maskX + this.computedMaskWidth / 2;
      const maskCenterY = this.maskY + this.computedMaskHeight / 2;
      return {
        position: 'absolute',
        width: (this.areaWidth * effectiveScale) + 'px',
        height: (this.areaHeight * effectiveScale) + 'px',
        transform: `translate(${this.zoomWidthPx / 2 - maskCenterX * effectiveScale}px, ${this.zoomHeightPx / 2 - maskCenterY * effectiveScale}px)`
      };
    }
  },
  mounted() {
    this.updateAreaDimensions();
    window.addEventListener('resize', this.updateAreaDimensions);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.updateAreaDimensions);
  },
  methods: {
    parseDimension(val) {
      if (typeof val !== 'string') return Number(val);
      if (val.endsWith('px')) {
        return parseFloat(val);
      } else if (val.endsWith('vw')) {
        return window.innerWidth * parseFloat(val) / 100;
      } else if (val.endsWith('vh')) {
        return window.innerHeight * parseFloat(val) / 100;
      } else {
        return parseFloat(val);
      }
    },
    updateAreaDimensions() {
      if (this.$refs.previewImg) {
        this.areaWidth = this.$refs.previewImg.offsetWidth;
        this.areaHeight = this.$refs.previewImg.offsetHeight;
      }
    },
    handleMouseEnter() {
      this.showZoom = true;
    },
    handleMouseMove(e) {
      const rect = this.$refs.previewImg.getBoundingClientRect();
      let x = e.clientX - rect.left - this.computedMaskWidth / 2;
      let y = e.clientY - rect.top - this.computedMaskHeight / 2;
      if (x < 0) x = 0;
      if (y < 0) y = 0;
      if (x + this.computedMaskWidth > this.areaWidth) {
        x = this.areaWidth - this.computedMaskWidth;
      }
      if (y + this.computedMaskHeight > this.areaHeight) {
        y = this.areaHeight - this.computedMaskHeight;
      }
      this.maskX = x;
      this.maskY = y;
    },
    handleMouseLeave() {
      this.showZoom = false;
    },
    handleTouchStart(e) {
      e.preventDefault();
      this.showZoom = true;
      if (e.touches && e.touches.length > 0) {
        const touch = e.touches[0];
        this.handleMouseMove({ clientX: touch.clientX, clientY: touch.clientY });
      }
    },
    handleTouchMove(e) {
      e.preventDefault();
      if (e.touches && e.touches.length > 0) {
        const touch = e.touches[0];
        this.handleMouseMove({ clientX: touch.clientX, clientY: touch.clientY });
      }
    },
    handleTouchEnd(e) {
      e.preventDefault();
      this.showZoom = false;
    }
  }
};
</script>

<style scoped>
.magnifier-mask {
  border: 1px solid #fff;
}
.magnifier-zoom {
  border: 1px solid #ccc;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
}
.zoom-adjuster {
  position: absolute;
  width: 0px;
  overflow: visible;
  right: 55px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
}
.zoom-slider {
  -webkit-appearance: none;
  appearance: none;
  width: 150px;
  height: 8px;
  background: #ddd;
  outline: none;
  opacity: 0.7;
  transition: opacity .2s;
  transform: rotate(-90deg);
  border-radius: 1em;
}
.zoom-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  border: 2px solid #ccc;
  cursor: pointer;
}
.zoom-slider::-moz-range-thumb {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  border: 2px solid #ccc;
  cursor: pointer;
}
</style>
