<script>
import _Services from "./_Services.vue";
export default {
  extends:_Services,

  methods: {
    validateAndSubmitForm: function (e, submitForm) {
      const form = e.target; // Dynamically get the form from the event
      const inputs = Array.from(form.querySelectorAll('.form-control')); // Gather all inputs

      let allValid = true;

      inputs.forEach(input => {
        if (!input.checkValidity()) {
          input.reportValidity();
          allValid = false;
        }
      });

// If valid, convert form data to JSON
      if (allValid) {
        const formData = inputs.reduce((acc, input) => {
          acc[input.name] = input.value.trim(); // Use input's name as key and value
          return acc;
        }, {});

        submitForm(formData);
      }
    }
  }
}
</script>
