<template>
  <div v-show="active" class="camera-container" :class="{ landscape: isLandscape }">
    <div v-if="loading" class="loading-overlay">
      <div class="cam-spinner"></div>
    </div>

    <video
        v-show="!showConfirm && !loading"
        autoplay
        playsinline
        class="media-view"
    ></video>
    <div class="framing-overlay"></div>
    <img
        v-show="showConfirm && !loading"
        :src="currentImageData || ''"
        class="media-view"
    />

    <div class="top-controls">
      <div class="control-item orientation-toggle" @click="toggleOrientation">
        <i class="fas fa-sync-alt"></i>
      </div>
      <div class="control-item auto-toggle" @click="cycleAutoApproveOption">
        Auto:
        <span v-show="autoApproveDurations[selectedAutoApproveIndex] === 0">Off</span>
        <span v-show="autoApproveDurations[selectedAutoApproveIndex] !== 0">{{ (autoApproveDurations[selectedAutoApproveIndex] / 1000) + 's' }}</span>
      </div>
      <div class="control-item close-camera-btn" @click="$write('nav.camera', false);">
        <i class="fas fa-times"></i>
      </div>
    </div>

    <div class="controls">
      <div class="capture-controls" v-show="!showConfirm && !loading">
        <button class="icon-btn capture" :disabled="isCapturing" @click="capturePhoto">
          <i v-if="!isCapturing" class="fas fa-camera"></i>
          <i v-else class="fas fa-spinner fa-spin"></i>
        </button>
      </div>
      <div class="confirm-controls" v-show="showConfirm && !loading">
        <!-- Currency Code Dropdown -->
        <div class="camera-dropdown" v-if="$read('camera.currency')">
          <button style="line-height: 1em;" class="icon-btn dropdown-toggle" @click.stop="toggleDropdown">
            {{ selectedCurrency }}
          </button>
          <div v-show="dropdownVisible" class="camera-dropdown-menu">
            <ul>
              <li v-for="currency in $read('currencies.options')" :key="currency" @click="selectDropdownOption(currency)">
                {{ currency }}
              </li>
            </ul>
          </div>
        </div>
        <button class="icon-btn confirm" :disabled="isUploading" @click="usePhoto">
          <i v-if="!isUploading" class="fas fa-check"></i>
          <i v-else class="fas fa-spinner fa-spin"></i>
        </button>
        <button class="icon-btn retake" @click="retakePhoto">
          <i class="fas fa-undo"></i>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { nextTick } from 'vue';
import _Services from "./_Services.vue";

export default {
  extends: _Services,
  data() {
    return {
      currentImageData: null,
      showConfirm: false,
      videoElement: null,
      stream: null,
      loading: false,
      autoApproveTimer: null,
      autoApproveDurations: [0, 1000, 3000, 5000],
      captureCanvas: null,
      imageCapture: null,
      dropdownVisible: false,
      isCapturing: false,
      isUploading: false,
    };
  },
  computed: {
    selectedCurrency: {
      get(){
        return stateTagApp.$read('currencies.selected');
      },
      set(value){
        stateTagApp.$write('currencies.selected', value);
      }
    },

    // Persisted Property: isLandscape
    isLandscape: {
      get() {
        return stateTagApp.$read('camera.landscape');
      },
      set(value) {
        stateTagApp.$write('camera.landscape', value);
      },
    },
    // Persisted Property: selectedAutoApproveIndex
    selectedAutoApproveIndex: {
      get() {
        return stateTagApp.$read('camera.auto');
      },
      set(value) {
        stateTagApp.$write('camera.auto', value);
      },
    },
    // Persisted Property: endpointUrl
    endpointUrl: {
      get() {
        return stateTagApp.$read('camera.endpoint');
      },
      set(value) {
        stateTagApp.$write('camera.endpoint', value);
      },
    },
    // Active State
    active() {
      return stateTagApp.$read('nav.camera');
    },
  },
  watch: {
    active(val, oldVal) {
      if (!oldVal && val) {
        this.startCamera();
      } else if (oldVal && !val) {
        this.stopCamera();
      }
    },
  },

  mounted() {
    this.setupMenu().then(() => {
      if(this.locus != 'currencies.selected'){
        this.$write(this.locus, this.$read('currencies.selected'));
      }
    }) ;

    this.videoElement = this.$el.querySelector('video');
    this.captureCanvas = document.createElement('canvas');
    document.addEventListener('click', this.handleClickOutside);
    if (this.active) {
      this.startCamera();
    }
  },
  beforeDestroy() {
    document.removeEventListener('click', this.handleClickOutside);
  },
  methods: {
    setupMenu: function () {
      return this.$api(endPoints.currencies, 'setupCurrencyMenu')
          .then(result => {
            this.$write('currencies.options', result.data);
          });
    },

    handleClickOutside(event) {
      const dropdown = this.$el.querySelector('.camera-dropdown');
      if (dropdown && !dropdown.contains(event.target)) {
        this.dropdownVisible = false;
      }
    },
    toggleDropdown() {
      this.dropdownVisible = !this.dropdownVisible;
    },
    selectDropdownOption(currency) {
      this.selectedCurrency = currency;
      this.dropdownVisible = false;
      stateTagApp.$write('currencies.selected', currency);
      //console.log('Selected currency:', currency);
    },
    async startCamera() {
      this.loading = true;
      try {
        this.stream = await navigator.mediaDevices.getUserMedia({
          video: {
            facingMode: 'environment'
          },
          audio: false,
        });
        if (this.videoElement) {
          this.videoElement.srcObject = this.stream;
          await this.videoElement.play();
          const [track] = this.stream.getVideoTracks();
          this.imageCapture = new ImageCapture(track);
        }
      } catch (error) {
        console.error('Error accessing camera:', error);
        alert('Unable to access camera. Check permissions and try again.');
      } finally {
        this.loading = false;
      }
    },
    async capturePhoto() {
      this.isCapturing = true;
      if (!this.imageCapture) {
        console.warn('ImageCapture not ready');
        this.simpleCapture();
        return;
      }
      try {
        const blob = await this.imageCapture.takePhoto();
        const img = new Image();
        const url = URL.createObjectURL(blob);
        img.onload = () => {
          // Full image dimensions
          const vw = img.naturalWidth;
          const vh = img.naturalHeight;
          // Measure overlay vs video in CSS px
          const overlay = this.$el.querySelector('.framing-overlay');
          const overlayRect = overlay.getBoundingClientRect();
          const videoRect   = this.videoElement.getBoundingClientRect();
          // Map overlay to sensor pixels using object-fit cover
          const containerWidth  = videoRect.width;
          const containerHeight = videoRect.height;
          const coverScale = Math.max(
            containerWidth  / vw,
            containerHeight / vh
          );
          // dimensions of sensor area visible in the container
          const visibleW = containerWidth  / coverScale;
          const visibleH = containerHeight / coverScale;
          // sensor-space offset of the visible window
          const deltaX = (vw - visibleW) / 2;
          const deltaY = (vh - visibleH) / 2;
          // overlay position in CSS px
          const overlayX = overlayRect.left - videoRect.left;
          const overlayY = overlayRect.top  - videoRect.top;
          // final crop coords in sensor pixels
          const sx = deltaX + overlayX / coverScale;
          const sy = deltaY + overlayY / coverScale;
          const sw = overlayRect.width  / coverScale;
          const sh = overlayRect.height / coverScale;
          // Draw to canvas
          this.captureCanvas.width = sw;
          this.captureCanvas.height = sh;
          const ctx = this.captureCanvas.getContext('2d', { willReadFrequently: true });
          ctx.drawImage(img, sx, sy, sw, sh, 0, 0, sw, sh);
          URL.revokeObjectURL(url);
          this.currentImageData = this.captureCanvas.toDataURL('image/png');
          this.showConfirm = true;
          this.startAutoApproveTimer();
          this.isCapturing = false;
        };
        img.src = url;
      } catch (error) {
        console.error('ImageCapture error:', error);
        // Fallback: simple canvas from video
        this.simpleCapture();
      }
    },
    simpleCapture() {
      // original canvas fallback
      if (!this.videoElement) return;
      const vw = this.videoElement.videoWidth;
      const vh = this.videoElement.videoHeight;
      const cw = this.videoElement.clientWidth;
      const ch = this.videoElement.clientHeight;
      const displayAspect = cw / ch;
      const videoAspect = vw / vh;
      let sx, sy, sw, sh;
      if (videoAspect > displayAspect) {
        sw = vh * displayAspect;
        sh = vh;
        sx = (vw - sw) / 2;
        sy = 0;
      } else {
        sw = vw;
        sh = vw / displayAspect;
        sx = 0;
        sy = (vh - sh) / 2;
      }
      this.captureCanvas.width = sw;
      this.captureCanvas.height = sh;
      const ctx = this.captureCanvas.getContext('2d', { willReadFrequently: true });
      ctx.drawImage(this.videoElement, sx, sy, sw, sh, 0, 0, sw, sh);
      this.currentImageData = this.captureCanvas.toDataURL('image/png');
      this.showConfirm = true;
      this.startAutoApproveTimer();
      this.isCapturing = false;
    },
    async retakePhoto() {
      this.clearAutoApproveTimer();
      this.showConfirm = false;
      this.currentImageData = null;
      if (this.videoElement && this.stream) {
        this.videoElement.srcObject = this.stream;
        await this.videoElement.play();
      }
      this.isCapturing = false;
    },
    async usePhoto() {
      this.clearAutoApproveTimer();
      this.isUploading = true;
      try {
        const response = await fetch(this.currentImageData);
        if (!response.ok) {
          throw new Error('Failed to fetch image data.');
        }
        const blob = await response.blob();
        const formData = new FormData();

        if(stateTagApp.$read('camera.currency')){
          formData.append('currency', stateTagApp.$read('currencies.selected'));
        }

        formData.append('file', blob, 'photo.png');
        this.$api(this.endpointUrl, 'Builder', formData, undefined, { 'Content-Type': 'multipart/form-data' })
            .then((response) => {
              //console.log('Photo uploaded successfully:', response);
              this.$broadcast({ from: 'Project', type: 'api', event: 'project-updated', ...response.data });
              if (this.endpointUrl.includes('receipts')) {
                //console.log('receipt uploaded successfully');
                stateTagApp.$read('ai').activated = 1;;
              } else {
                //console.log('project image uploaded successfully');
                let url = endPoints.files.list.replace('{project}', this.$read('project.id'));
                this.$api(url, 'Builder')
                    .then((response) => {
                      this.$write('builder.files', response.data);
                    })
                    .catch((error) => {
                      //console.log(error);
                    });
              }
            })
            .catch((apiError) => {
              console.error('Upload failed:', apiError);
              alert('Upload failed. Check console for details.');
              this.$write('errors.builder', this.$translate('errors.saving'));
            })
            .finally(async () => {
              this.isUploading = false;
              this.showConfirm = false;
              this.currentImageData = null;
              if (this.videoElement && this.stream) {
                this.videoElement.srcObject = this.stream;
                try {
                  await this.videoElement.play();
                } catch (playError) {
                  console.error('Error playing video stream:', playError);
                }
              }
            });
      } catch (error) {
        console.error('Error processing image:', error);
        alert('Failed to process the image. Please try again.');
        this.$write('errors.builder', this.$translate('errors.processing'));
        return;
      }
    },
    toggleOrientation() {
      this.isLandscape = !this.isLandscape;
    },
    async stopCamera() {
      if (this.stream) {
        this.stream.getTracks().forEach((track) => track.stop());
        this.stream = null;
      }
      if (this.videoElement) {
        this.videoElement.srcObject = null;
      }
    },
    startAutoApproveTimer() {
      this.clearAutoApproveTimer();
      const delay = this.autoApproveDurations[this.selectedAutoApproveIndex];
      if (delay > 0 && this.showConfirm) {
        this.autoApproveTimer = setTimeout(() => {
          this.usePhoto();
        }, delay);
      }
    },
    clearAutoApproveTimer() {
      if (this.autoApproveTimer) {
        clearTimeout(this.autoApproveTimer);
        this.autoApproveTimer = null;
      }
    },
    cycleAutoApproveOption() {
      this.selectedAutoApproveIndex =
          (this.selectedAutoApproveIndex + 1) % this.autoApproveDurations.length;
      this.startAutoApproveTimer();
    },
  },
};
</script>

<style scoped>
.camera-dropdown {
  position: relative;
  display: inline-block;
}

.camera-dropdown-menu {
  position: absolute;
  max-height: 30vh;
  overflow: scroll;
  bottom: 100%;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  min-width: 100px;
  border-radius: 5px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  padding: 5px 0;
  z-index: 1000;
}

.camera-dropdown-menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.camera-dropdown-menu ul li {
  padding: 8px 15px;
  cursor: pointer;
}

.camera-dropdown-menu ul li:hover {
  background: rgba(255, 255, 255, 0.1);
}
.camera-container {
  position: relative;
  overflow: visible;
}
.framing-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80%;
  height: 80%;
  transform: translate(-50%, -50%);
  border: none;
  pointer-events: none;
  box-sizing: border-box;
  z-index: 10;
}
</style>
