<template>
  <transition name="ai-activated-slide">
    <div v-if="visible" class="ai-activated-banner">
      {{ $translate('ai') }}
    </div>
  </transition>
</template>

<script>
import _Services from "./_Services";

export default {
  extends: _Services,

  data: function () {
    return {
      visible: false,
      lastAiValue: 0,
      poller: null,
    };
  },

  computed: {
    aiActivated() {
      return stateTagApp.$read('ai').activated;
    },
    aiNotice() {
      return stateTagApp.$read('ai').model + ':' + stateTagApp.$read('ai').id;
    }
  },

  watch: {
    aiActivated(newVal) {
      if (newVal && newVal !== this.lastAiValue) {
        this.lastAiValue = newVal;
        this.triggerBanner();
      }
    },
    aiNotice(newVal) {
      let [model, id] = newVal.split(':', 2);

      // STAY ON THE CURRENT PROJECT NO MATTER WHAT THE NOTIFICATION SAYS.
      let project = stateTagApp.$read('project');

      switch (model) {
        case 'project':

          if (id != project.id) {
            return;
          }

          project.status = 'active';
          stateTagApp.$write('project', project);
          break;

        case 'receipt':
          this.loadReceipts();
          break;

        case 'search':
          alert(this.$translate('instructions.search-rendered'));

          const url = endPoints.search.history.replace("{project}", project.id);
          this.$api(url, "SearchNav")
              .then(response => {
                stateTagApp.$write("searches", response.data);
              });

          break;
      }
    }
  },
  methods: {
    loadReceipts() {
      let url = endPoints.receipts.list.replace("{project}", this.projectId);
      url = this.epochGetString(url);

      this.$api(url, "Receipts")
          .then((response) => {
            this.$write("receipts", response.data);
          })
          .catch((error) => {
            //console.error(error);
          });
    },
    triggerBanner() {
      this.visible = true;

      setTimeout(() => {
        this.visible = false;
        this.lastAiValue = 0;
        stateTagApp.$write('ai.activated', 0);
      }, 4000);
    }
  }
};
</script>

<style>
@keyframes gradientShift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.ai-activated-banner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  font-size: 1em;
  line-height: 2.25em;

  padding-top: .5rem !important;
  padding-bottom: .5rem !important;

  /*font-weight: bold;*/
  color: #333333;
  text-align: center;
  opacity: 0.70;
  z-index: 9999;
  overflow: hidden;
  pointer-events: none;

  background: linear-gradient(270deg,
  #99eaff,
  #d8f59f,
  #fcb3dd,
  #ffc68f,
  #99eaff);
  background-size: 1000% 100%;
  animation: gradientShift 6s ease infinite;
}

.ai-activated-slide-enter-active,
.ai-activated-slide-leave-active {
  transition: opacity 0.3s, transform 0.3s;
}

.ai-activated-slide-enter,
.ai-activated-slide-leave-to {
  opacity: 0;
  transform: translateY(-100%);
}
</style>
