<template>
  <!-- We'll display the 3-level UI only if we can find the actual receipt -->
  <div v-if="actualReceipt" class="receipt-card">
    <!-- LEVEL 1: Single-line header. Instead of conditionally rendering it, we always render it
         and toggle the "header-hidden" class based on level2Visible -->
    <div
        class="receipt-card-header"
        :class="{'header-hidden': level2Visible}"
        data-bs-toggle="collapse"
        :data-bs-target="`#${collapseLevel2Id}`"
        aria-expanded="false"
        :aria-controls="collapseLevel2Id"
    >
      <div class="header-date" style="white-space: nowrap;">{{ actualReceipt.nice }}</div>
      <div class="header-store">{{ truncatedStoreName }}</div>
      <div class="header-flags" v-html="indicatorIconsShort"></div>
      <div class="header-amount">{{ actualReceipt.symbol }}{{ actualReceipt.total | numberFormat(false) }}</div>
    </div>

    <!-- LEVEL 2: Expanded card details -->
    <div class="collapse" :id="collapseLevel2Id">
      <div class="receipt-card-body">
        <div class="d-flex justify-content-between align-items-start mb-2"
             data-bs-toggle="collapse"
             :data-bs-target="`#${collapseLevel2Id}`"
             style="cursor: pointer;"
        >
          <div>
            <!-- Bold store name toggles Level 2 collapse -->
            <div class="receipt-title">
              {{ actualReceipt.storeName }}
            </div>
            <div class="mt-1" v-html="indicatorIconsFull"></div>
          </div>
        </div>

        <div class="row stats-row">
          <div class="col-4 stats-col mb-2">
            <small>{{ $translate('receipt.date') }}</small>
            <span>{{ actualReceipt.date }}</span>
          </div>
          <div class="col-4 stats-col mb-2">
            <small>{{ $translate('receipt.currency') }}</small>
            <span>{{ actualReceipt.currency }}</span>
          </div>
          <div class="col-4 stats-col mb-2">
            <small>{{ $translate('receipt.total') }}</small>
            <span class="fw-bold">
              {{ actualReceipt.symbol }}{{ actualReceipt.total | numberFormat(true) }}
            </span>
          </div>
        </div>

        <p class="receipt-summary">
          {{ actualReceipt.summary[$read('langpack.selected')] }}
        </p>

        <div class="btn-group col-12" role="group">
          <!-- Button for Items -->
          <button
              class="btn btn-sm btn-primary btn-items"
              type="button"
              data-bs-toggle="collapse"
              :data-bs-target="`#${collapseLevel3Id}`"
              aria-expanded="false"
              :aria-controls="collapseLevel3Id"
              @click.stop="scrollOpenLevel3"
          >
            <i class="fas fa-list"></i>
          </button>

          <!-- Button for Photo (conditionally shown) -->
          <button
              v-if="actualReceipt.photo"
              class="btn btn-sm btn-secondary btn-photo"
              type="button"
              @click.prevent="showPhotoModal(actualReceipt.photo)"
          >
            <i class="fas fa-camera"></i>
          </button>
        </div>

        <!-- LEVEL 3: Items Table -->
        <div class="collapse items-collapse mt-3" :id="collapseLevel3Id">
          <div class="table-responsive">
            <table class="table table-sm items-table">
              <thead>
              <tr>
                <th>{{ $translate('receipt.item') }}</th>
                <th class="text-center">{{ $translate('receipt.quantity') }}</th>
                <th class="text-end">{{ $translate('receipt.price') }}</th>
                <th class="text-center">&nbsp;</th>
              </tr>
              </thead>
              <tbody>
              <template v-for="(item, idx) in actualReceipt.items">
                <tr>
                  <td>{{ item.description[$read('langpack.selected')] }}</td>
                  <td class="text-center">{{ item.units || 1 }}</td>
                  <td class="text-end">{{ actualReceipt.symbol }}{{ item.price | numberFormat(true) }}</td>
                  <td class="text-end"><x-receipt-flags :value="item.flags" :index="idx" @select="handleFlagSelect"/></td>
                </tr>
                <tr>
                  <td colspan="4" class="receipt-summary">
                    <small>{{ item.usage[$read('langpack.selected')] }}</small>
                  </td>
                </tr>
              </template>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _Services from "@/x-html/_Services";

// These define the different flags used across receipts and their visual appearance
const ICONS_MAP = {
  irrelevant: { icon: "fa-ban", color: "secondary", title: "receipt.indicators.irrelevant" },
  excessive: { icon: "fa-exclamation-triangle", color: "warning", title: "receipt.indicators.excessive" },
  duplicate: { icon: "fa-clone", color: "primary", title: "receipt.indicators.duplicate" },
  suspicious: { icon: "fa-question-circle", color: "danger", title: "receipt.indicators.suspicious" },
  noncompliant: { icon: "fa-exclamation-circle", color: "danger", title: "receipt.indicators.noncompliant" },
};

export default {
  name: "XReceipt",
  extends: _Services,

  props: {
    // This is the index into 'receipts' (a Number), not the full object.
    receipt: {
      type: Number,
      required: true,
    },
  },

  data() {
    return {
      collapseLevel2Id: `collapse-lvl2-${this._uid}`,
      collapseLevel3Id: `collapse-lvl3-${this._uid}`,
      modalId: `photoModal-${this._uid}`,
      level2Visible: false,
    };
  },

  computed: {
    // Read the actual object from the store using the receipt index
    actualReceipt() {
      const index = this.receipt;
      const allReceipts = this.$read("receipts");
      if (!Array.isArray(allReceipts) || !allReceipts[index]) return null;
      return allReceipts[index];
    },

    truncatedStoreName() {
      if (!this.actualReceipt) return "";
      const maxLen = 30;
      const name = this.actualReceipt.storeName || "";
      return name.length <= maxLen ? name : name.slice(0, maxLen - 3) + "...";
    },

    // Collapse all item-level flags into a single icon set for the level 1 header
    indicatorIconsShort() {
      if (!this.actualReceipt) return "";
      let allFlags = [];
      if (Array.isArray(this.actualReceipt.items)) {
        this.actualReceipt.items.forEach(item => {
          if (Array.isArray(item.flags)) {
            allFlags.push(...item.flags);
          }
        });
      }
      if (allFlags.length === 0) return "";
      const flagCounts = {};
      allFlags.forEach(flag => {
        flagCounts[flag] = (flagCounts[flag] || 0) + 1;
      });
      const uniqueCount = Object.keys(flagCounts).length;
      const totalCount = allFlags.length;
      let iconsHtml = Object.keys(flagCounts)
          .map(flag => {
            const iconData = ICONS_MAP[flag];
            if (!iconData) return "";
            let title = this.$translate(iconData.title);
            return `<i class="fas ${iconData.icon} text-${iconData.color} me-1" title="${title}"></i>`;
          })
          .join("");
      // Only add the badge if there's at least one duplicate flag.
      if (totalCount > uniqueCount) {
        iconsHtml += `<span class="badge-counter">${totalCount}</span>`;
      }
      return iconsHtml;
    },

    // Aggregate flags across all purchased items for the full detail view
    indicatorIconsFull() {
      if (!this.actualReceipt) return "";
      let allFlags = [];
      if (Array.isArray(this.actualReceipt.items)) {
        this.actualReceipt.items.forEach(item => {
          if (Array.isArray(item.flags)) {
            allFlags.push(...item.flags);
          }
        });
      }
      if (allFlags.length === 0) return "";
      const flagCounts = {};
      allFlags.forEach(flag => {
        flagCounts[flag] = (flagCounts[flag] || 0) + 1;
      });
      return Object.entries(flagCounts)
          .map(([flag, count]) => {
            const iconData = ICONS_MAP[flag];
            if (!iconData) return "";
            let title = this.$translate(iconData.title);
            let html = `<span class="badge bg-${iconData.color} indicator-badge me-2" title="${title}">
            <i class="fas ${iconData.icon}"></i> ${title}</span>`;
            if (count > 1) {
              html += `<span class="badge-counter" style="position: relative; left: -1.6em; top: -0.5em; font-size: 0.7em;">${count}</span>`;
            }
            return html;
          })
          .join("");
    },
  },

  methods: {
    scrollToLevel3() {
      this.$nextTick(() => {
        const el = this.$el.querySelector(`#${this.collapseLevel3Id}`);
        if (el) {
          setTimeout(() => {
            el.scrollIntoView({ behavior: 'smooth', block: 'start' });
          }, 350); // wait for collapse animation to run
        }
      });
    },

    handleFlagSelect(args) {
      let flagging = { _method: "PUT", ...args.detail[0] };

      let url = endPoints.receipts.update
          .replace("{project}", this.$read("project.id"))
          .replace("{receipt}", this.actualReceipt.id);

      this.$api(url, "ReceiptFlagUpdate", flagging, "PUT")
          .then(() => this.$emit("modified"))
          .catch(console.log);
    },

    showPhotoModal(photoUrl) {
      this.$write("modal.content.header", "");
      this.$write("modal.content.body", " ");
      this.$write("modal.content.image", photoUrl);
    },
  },

  mounted() {
    const container = this.$el.closest('#main-container'); // adjust class if needed

    const scrollToEl = (targetEl) => {
      if (!container || !targetEl) return;

      const containerRect = container.getBoundingClientRect();
      const targetRect = targetEl.getBoundingClientRect();

      const offset = targetRect.top - containerRect.top + container.scrollTop - 10;
      container.scrollTo({ top: offset, behavior: 'smooth' });
    };

    // LEVEL 2
    const collapseEl = this.$el.querySelector(`#${this.collapseLevel2Id}`);
    if (collapseEl) {
      collapseEl.addEventListener("show.bs.collapse", () => {
        this.level2Visible = true;
      });

      collapseEl.addEventListener("hide.bs.collapse", () => {
        this.level2Visible = false;
      });

      collapseEl.addEventListener("shown.bs.collapse", () => {
        setTimeout(() => scrollToEl(collapseEl), 50);
      });
    }

    // LEVEL 3
    const collapseLevel3El = this.$el.querySelector(`#${this.collapseLevel3Id}`);
    if (collapseLevel3El) {
      collapseLevel3El.addEventListener("hide.bs.collapse", (e) => e.stopPropagation());

      collapseLevel3El.addEventListener("shown.bs.collapse", () => {
        setTimeout(() => scrollToEl(collapseLevel3El), 50);
      });
    }
  }

};
</script>

