.badge-counter {
  display: inline-block;
  background: linear-gradient(135deg, #ff6b81, #ff4757);
  color: #fff;
  border-radius: 50%;
  padding: 0.2em 0.6em;
  font-size: 0.75rem;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
